.animated-icon {
  -webkit-animation: pulse 3s infinite steps(2);
  animation: pulse 3s infinite steps(2);
  -webkit-animation-direction: alternate-reverse;
  animation-direction: alternate-reverse;
  -webkit-animation-timing-function: cubic-bezier(0.52, 0.48, 0.52, 0.56);
  animation-timing-function: cubic-bezier(0.52, 0.48, 0.52, 0.56);
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both; }

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1); }
  100% {
    -webkit-transform: scale(1.5);
    transform: scale(1.5); } }

@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1); }
  100% {
    -webkit-transform: scale(1.5);
    transform: scale(1.5); } }



